.p-404 {
  .main {
    position: relative;
    overflow: hidden;
  }
  h1 {
    color: white;
    font-weight: 700;
    font-size: 220px;
    line-height: 220px;
    text-shadow: white 1px 0 10px;
    @media(max-width: $mobile) {
      font-size: 120px;
      line-height: 120px;
    }
  }
  &-decor {
    position: absolute;
    top: 300px;
    left: -60px;
    opacity: 0.2;
  }
}